@import './src/presets/varialbes';
.-main-base {
  min-width: $MOBILE_MIN_WIDTH;
  box-sizing: border-box;
  width: 100%;
  position: relative;
}

.-sticky-box {
  //background-image: url("./images/cooler.png");
  background-repeat: no-repeat;
  background-position: top center;
  box-sizing: border-box;
  position: sticky;
  width: 100%;
  top: 0;
  left: 0;
  @include marginZero;
  // TEST
  //@include tempStickyLine;
}

.-title-box {
  box-sizing: border-box;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
}

@media only screen and (min-width: 0px) and (max-width: $MOBILE_MAX_WIDTH) {
  .-sticky-box {
    //background-size: cover;
  }
}

@media only screen and (min-width: $TABLET_MIN_WIDTH) and (max-width: $TABLET_MAX_WIDTH) {
  .-sticky-box {
    //background-size: cover;
  }
}

@media only screen and (min-width: $PC_MIN_WIDTH) {
  .-sticky-box {
    //background-position: top center;
    //background-size: cover;
  }
  .-title-box {
    max-width: 400px;
    top: 500px;
  }
}
